import React from 'react';
import { Helmet } from "react-helmet";
import cn from 'classnames';

import Contacts from "./components/Contacts";
import Documents from "./components/Documents";
import Partners from "./components/Partners";

import directionsService from '../../../../services/directions.service'

import { ReactComponent as YellowChevronDown } from '../../../../assets/images/yellow-cheron-down.svg';
import type { AboutDirection, Document } from "../../../../types";

type State = {
    data: AboutDirection,
    contentReadMore: boolean,
    docsReadMore: boolean
}

type Props = {
    title: string
}

export default class About extends React.Component<Props, State> {
    constructor(props) {
        super(props);

        this.state = {
            data: {
                trendsdocuments_set: [],
                trendscontacts_set: [],
                trendspartners_set: []
            },
            contentReadMore: false,
            docsReadMore: false
        }
    }

    componentDidMount() {
        directionsService.getDirectionAbout(this.props.match.params.id)
            .then(response => {
                this.setState({ data: response.data })
            })
    }

    componentDidUpdate(prevProps) {
        if(prevProps.location.pathname !== this.props.location.pathname) {
            directionsService.getDirectionAbout(this.props.match.params.id)
                .then(response => {
                    this.setState({ data: response.data })
                })
        }
    }

    handleContentReadMore = () => {
        this.setState({ contentReadMore: true });
    };

    renderDocuments = (): Array<Document> => {
        if(this.state.docsReadMore) {
            return this.state.data.trendsdocuments_set;
        } else {
            return this.state.data.trendsdocuments_set.slice(0, 4);
        }
    };

    render() {
        return (
            <React.Fragment>
                <Helmet>
                    <title>{`${this.props.title} - О направлении`}</title>
                </Helmet>

                {this.state.data.text &&
                    <div className="direction-page__block">
                        <div
                            className={cn('direction-page__content')}>
                            {
                                !this.state.contentReadMore &&
                                this.state.data.text.length > 300 ?
                                    this.state.data.text.slice(0, 300) + '...' :
                                    this.state.data.text}
                        </div>

                        {
                            !this.state.contentReadMore && this.state.data.text.length > 300 &&
                            (<button className="direction-page__show-content"
                                    onClick={() => this.handleContentReadMore()}>
                                <span>Читать далее</span>
                                <YellowChevronDown/>
                            </button>)
                        }
                    </div>
                }

                <div className="direction-page__block">
                    <h3 className="territory-info__title">Контакты</h3>

                    <div className="committee-personal-list committee-personal-list--direction">
                        {this.state.data.trendscontacts_set.map(item => (
                            <Contacts {...item} key={item.id} />
                        ))}
                    </div>
                </div>

                <div className="direction-page__block">
                    <h3 className="territory-info__title">Документы</h3>

                    <div className="doc-list">
                        {this.renderDocuments().map(item => (
                            <Documents key={item.id} {...item} />
                        ))}
                    </div>
                    {
                        !this.state.docsReadMore && this.state.data.trendsdocuments_set.length > 4 &&
                        (<button className="direction-page__show-content">
                            <span>Показать все</span>
                            <YellowChevronDown />
                        </button>)
                    }
                </div>

                <div className="direction-page__block">
                    <h3 className="territory-info__title">Партнеры направления</h3>

                    <div className="partners-list">
                        {this.state.data.trendspartners_set.map(item => (
                            <Partners key={item.id} {...item} />
                        ))}
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
