import React from 'react';
import styles from './TerritoryBlock.module.scss';
import territoryImage from './images/territory.png';
import { Link } from "react-router-dom";
import type {Territory} from "../../../../../types";
import territoryService from '../../../../../services/territory.service'

type State = {
    territories: {
        cities: Array<Territory>,
        districts: Array<Territory>,
    }
}

export default class TerritoryBlock extends React.Component<{}, State>{
    constructor(props) {
        super(props);

        this.state = {
            territories: {
                cities: [],
                districts: [],
            }
        }
    }

    componentDidMount(): void {
        territoryService.getTerritoryList()
            .then(response => {
                this.setState({ territories: response.data })
            })
    }

    render() {
        return (
            <section className="territory-block">
                <div className="container">
                    <h2 className="common-title">территории</h2>

                    <div className={styles.block}>
                        <div className={styles.map}>
                            <img src={territoryImage} alt=""/>
                        </div>
                        <div className={styles.content}>
                            <div className={styles.object}>территории ростовской области</div>
                            <div className={styles.info}>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td>1 000 000</td>
                                            <td>Молодых<br /> пользователей</td>
                                        </tr>

                                        <tr>
                                            <td>43</td>
                                            <td>Муниципальных<br /> района</td>
                                        </tr>

                                        <tr>
                                            <td>17</td>
                                            <td>Городских<br /> округа</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div className="text-right mt-4">
                                {this.state.territories.cities.length > 0 &&
                                    <Link className="common-button common-button--territory" to={`/territory/${this.state.territories.cities[0].id}`}>Все территории</Link>
                                }
                            </div>

                        </div>
                    </div>
                </div>
            </section>
        )
    }
}


