import React from 'react';
import { Helmet } from "react-helmet";

import helpers from '../../../helpers';

import type { ReactRef } from "../../../types";

import {ReactComponent as ButtonIcon} from "../../../assets/images/search-page-button.svg";
import { Link} from "react-router-dom";

type State = {
    query: string,
    input: ReactRef<HTMLInputElement>
}

export default class Search extends React.Component<{}, State> {
    constructor(props) {
        super(props);

        this.state = {
            query: helpers.getSearchQuery(props.location.search),
            input: React.createRef()
        }
    }

    handleSubmitForm = (e: Event) => {
        e.preventDefault();
        this.setState({ query: this.state.input.current.value });

        this.props.history.push({
            pathname: '/search',
            search: `q=${this.state.input.current.value}`
        })
    };

    render() {
        return (
            <React.Fragment>
                <Helmet>
                    <title>Результаты поиска {this.state.query}</title>
                </Helmet>

                <div className="search-page">
                    <div className="container">
                        <h1 className="main-title">Результаты поиска {this.state.query && <b>{this.state.query}</b>}</h1>

                        <div className="search-form">
                            <form className={'d-flex'} onSubmit={this.handleSubmitForm}>
                                <input type="text" ref={this.state.input} defaultValue={this.state.query} placeholder="Текст поискового запроса" />
                                <button type={'submit'}>
                                    <ButtonIcon />
                                </button>
                            </form>
                        </div>

                        <div className="search-title">
                            По вашему запросу найдено: <b>97</b>
                        </div>

                        <div className="search-result">
                            {[0,1,2,3,4,5].map(item => (
                                <div className="search-result__item" key={item}>
                                    <div className="search-result__type">новости</div>
                                    <Link to={'/news'}>Мария Захарова: «Создавайте информационную среду, и вы сможете влиять на ситуацию»</Link>
                                    <p>По мнению спикера, в современном мире блогинг становится такой же работой, как и любая другая специальность. Он не подразумевает записи в трудовой книжке, но регистрация все равно существует: при достижении определенного...</p>
                                </div>
                            ))}
                        </div>

                        <button className={'show-more mt-5'}>Показать еще</button>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
