import React from 'react';
import styles from './Banner.module.scss';
import { ScreenWidth } from '../../../../../context'

import banner from './images/banner.jpg';
import logoOnBanner from './images/logo-on-banner.png';

export default class Banner extends React.Component {
    renderBanner(screenWidth: number) {
        if(screenWidth > 1010) {
            return (
                <img className="d-block w-100" src={banner} alt=""/>
            )
        } else {
            return (
                <div className={styles.mobileBanner}>
                    <img src={logoOnBanner} alt=""/>
                </div>
            )
        }
    }

    render() {
        return(
            <ScreenWidth.Consumer>
                {({screenWidth}) => (
                    this.renderBanner(screenWidth)
                )}
            </ScreenWidth.Consumer>

        )
    }
}
